import React, { useState } from "react";
import API from "../../../utils/Api";
import Swal from "sweetalert2";
import DateFnsUtils from "@date-io/date-fns";

import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Card,
  CardBody,
  Row,
  Input,
  Col,
} from "reactstrap";
import TextField from "@material-ui/core/TextField";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faFileImport,
  faWindowClose,
  faCalendarDay,
  faAnchor,
  faFileExcel,
  faFileDownload,
  faInfoCircle,
} from "@fortawesome/free-solid-svg-icons";

import FileTemplate from "../../../assets/template/Template.xlsx";

import { useAuth } from "../../../context/auth";
import { useProfile } from "../../../context/profile";

import Error from "../Error/Error";
import ShipNameSelect from "../Forms/Fields/ShipNameSelect";
import LinearProgressBarWithLabel from "../Common/LinearProgressBarWithLabel";
import DepartureDate from "../Forms/Fields/DepartureDate";
import ArrivalDate from "../Forms/Fields/ArrivalDate";

const NewPassengerManifests = (props) => {
  const authToken = useAuth().authTokens.access_token;
  const profile_context = useProfile();

  const [isProcessing, setIsProcessing] = useState(false);
  const [isError, setIsError] = useState(false);
  const [errorMessage, setErrorMessage] = useState("Invalid Input");
  const [isSelectedDeparture, setIsSelectedDeparture] = useState(false);

  const [excelFile, setExcelFile] = useState(null);
  const [departure, setDeparture] = useState(null);
  const [arrival, setArrival] = useState(null);
  const [transportNumber, setTransportNumber] = useState("");
  const [shipName, setShipName] = useState("");
  const [transportType, setTransportType] = useState(0);
  const [voyageRoute, setVoyageRoute] = useState("");

  const dateFns = new DateFnsUtils();

  const handleDepartureDateChange = () => {
    setIsSelectedDeparture(true);
  };

  const validate = () => {
    let d1 = dateFns.date(departure);
    let d2 = dateFns.date(arrival);

    if (dateFns.isBefore(d2, d1) || dateFns.isEqual(d1, d2)) {
      setIsError(true);
      setErrorMessage("Invalid Arrival Date");
      return false;
    }
    return true;
  };

  const getShipDetails = (shipId) => {
    API.get(`carrier/transport/ship/${shipId}`, {
      headers: {
        Authorization: "bearer " + authToken,
      },
    }).then((result) => {
      let data = result.data;
      if (data != null) {
        setVoyageRoute(data.routes);
        setTransportType(data.transportation_category_id);
      }
    });
  };

  const importFileHandler = (e) => {
    e.preventDefault();

    if (!validate()) {
      return null;
    }

    setIsProcessing(true);
    let token = authToken;
    let formData = new FormData();

    formData.append("profileFile", excelFile);
    formData.append("departure", dateFns.format(departure, "Y-M-dd H:m:s"));
    formData.append("arrival", dateFns.format(arrival, "Y-M-dd H:m:s"));
    formData.append("transport_no", parseInt(transportNumber));
    formData.append("ship_name", shipName);
    formData.append("transport_type", transportType);
    formData.append("route", voyageRoute);

    API.post("store/import/profiles", formData, {
      headers: {
        Authorization: "bearer " + token,
        "Content-Type": "multipart/form-data",
      },
    })
      .then((result) => {
        if (result.data.status) {
          setIsProcessing(false);
          setExcelFile(null);
          setDeparture(null);
          setArrival(null);
          setTransportNumber("");
          setTransportType("");
          setVoyageRoute("");

          Swal.fire(
            "Transaction Completed!",
            "Data Successfully Imported!",
            "success"
          );

          profile_context.loadPassengers();
          props.toggle();
        } else {
          setIsProcessing(false);
          Swal.fire("Transaction Failed!", result.data.message, "error");
        }
      })
      .catch((e) => {
        setIsProcessing(false);
        Swal.fire("Transaction Failed!", "Failed to Import Data!", "error");
      });
  };

  return (
    <div>
      <Modal
        backdrop={"static"}
        isOpen={props.isOpen}
        toggle={props.toggle}
        className="modal-lg"
        style={{ marginTop: "7%", marginLeft: "30%" }}
      >
        <ModalHeader style={{ lineHeight: "2" }} toggle={props.toggle}>
          Import Passenger Manifest <br />
        </ModalHeader>
        <form
          onSubmit={(e) => importFileHandler(e)}
          encType="multipart/form-data"
          id="excelFile"
        >
          <ModalBody>
            {isError && (
              <Col md={12} className="text-center m-0 p-0">
                <h6>
                  <Error className="p-0 m-0">{errorMessage}</Error>
                </h6>
              </Col>
            )}
            <Row className="p-2">
              <Col md={12} className="mt-0 pt-0 mb-2 text-danger">
                <h6>
                  <FontAwesomeIcon icon={faInfoCircle} /> Note: Please fill in
                  the required (*) fields and use only the required template
                </h6>
              </Col>
              <div className="col-md-6 p-2">
                <h6 className="text-primary ml-2">
                  <FontAwesomeIcon icon={faCalendarDay} /> Travel Dates
                </h6>
                <Card className="border-0">
                  <CardBody className="p-2">
                    <div>
                      <DepartureDate
                        pickedDate={handleDepartureDateChange}
                        departureDate={departure}
                        setDepartureDateChange={setDeparture}
                      />
                    </div>
                    <div className="mt-2">
                      <ArrivalDate
                        isSelectedDeparture={isSelectedDeparture}
                        departure={departure}
                        arrivalDate={arrival}
                        setArrivalDateChange={setArrival}
                      />
                    </div>
                  </CardBody>
                </Card>
              </div>
              <div className="col-md-6 p-2">
                <h6 className="text-primary">
                  <FontAwesomeIcon icon={faAnchor} /> Transport Details
                </h6>
                <Card className="border-0">
                  <CardBody className="p-1 m-1">
                    <div>
                      <ShipNameSelect
                        getShipDetails={getShipDetails}
                        setShipName={setShipName}
                      />
                    </div>
                    <div className="mt-2 mb-2">
                      <TextField
                        value={transportNumber}
                        onChange={(e) => setTransportNumber(e.target.value)}
                        label="Voyage Number"
                        fullWidth
                        required
                      />
                    </div>
                    <div>
                      <TextField
                        label="Voyage Route"
                        value={voyageRoute}
                        onChange={(e) => setVoyageRoute(e.target.value)}
                        fullWidth
                        required
                        disabled={true}
                      />
                    </div>
                  </CardBody>
                </Card>
              </div>
              <div className="col-md-12 pt-2">
                <h6 className="text-success">
                  <FontAwesomeIcon icon={faFileExcel} /> Import Excel File:{" "}
                  <a
                    href={FileTemplate}
                    title="Download Passenger Manifest Template"
                    download="Passenger-Manifest-Template.xlsx"
                  >
                    <span style={{ fontSize: "1rem" }}>
                      <FontAwesomeIcon icon={faFileDownload} /> Download this
                      template
                    </span>
                  </a>
                  <br />
                </h6>
                <Card>
                  <CardBody className="p-1 m-0">
                    <div className="m-0">
                      <Input
                        accept=".xlsx,.xls"
                        id="raised-button-file"
                        type="file"
                        onChange={(e) => setExcelFile(e.target.files[0])}
                        disabled={isProcessing}
                        required
                      />
                    </div>
                  </CardBody>
                </Card>
              </div>
            </Row>
            <div>
              {isProcessing && (
                <Row className="p-2">
                  <Col className="text-center">
                    <LinearProgressBarWithLabel title="Importing Passenger Manifest" />
                  </Col>
                </Row>
              )}
            </div>
          </ModalBody>
          <ModalFooter>
            <Button
              type="submit"
              color="primary"
              disabled={isProcessing}
              style={{ minWidth: "120px" }}
            >
              <FontAwesomeIcon icon={faFileImport} /> Import
            </Button>{" "}
            <Button
              color="danger"
              onClick={props.toggle}
              disabled={isProcessing}
              style={{ minWidth: "120px" }}
            >
              <FontAwesomeIcon icon={faWindowClose} /> Cancel
            </Button>
          </ModalFooter>
        </form>
      </Modal>
    </div>
  );
};

export default NewPassengerManifests;
