import React, { useEffect, useState } from "react" 
import { Modal, ModalHeader, ModalBody, Row, Col } from "reactstrap" 
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome" 
import { faUserCog } from "@fortawesome/free-solid-svg-icons" 
import LinearProgressBarWithLabel from "../Common/LinearProgressBarWithLabel" 
import TransportDetailsForm from "../Forms/TransportDetailsForm" 
import { useProfile } from "../../../context/profile";
import { useTheme } from "../../../context/theme";

const TransportDetails = (props) => {
  const [isProcessing, setIsProcessing] = useState(false) 
  const selectedProfile = useProfile().selectedProfile;
  const modalMarginXL = useTheme().modalMarginXL;
  
  const [fname, setFname] = useState("")
  const [lname, setLname] = useState("")

  useEffect(() => {
    setFname(selectedProfile[2].first_name)
    setLname(selectedProfile[2].last_name)
  },[selectedProfile])

  return (
    <div>
      <Modal
        backdrop={"static"}
        isOpen={props.isOpen}
        toggle={props.toggle}
        className="modal-xl"
        style={{ marginTop: "5%", marginLeft: modalMarginXL }}
      >
        <ModalHeader style={{ lineHeight: "2" }} toggle={props.toggle}>
          <FontAwesomeIcon icon={faUserCog} /> Transport Details: <span className="text-success">{`${fname.toUpperCase()} ${lname.toUpperCase()}`}</span>
        </ModalHeader>
        <form onSubmit={(e) => e.preventDefault() }>
          <ModalBody className="pt-0">
            <Row>
              <TransportDetailsForm
                setIsProcessing={setIsProcessing}
                toggle={props.toggle}
              />
            </Row>
            <div>
              {isProcessing && (
                <Row className="p-2">
                  <Col className="text-center">
                    <LinearProgressBarWithLabel title="Updating Transport Details" />
                  </Col>
                </Row>
              )}
            </div>
          </ModalBody>
        </form>
      </Modal>
    </div>
  ) 
} 

export default TransportDetails 
